#canva{
  width: 40%;
  display: flex;
  flex-direction: column;
  align-items: center;

  #imgPkm{
    width: 50%;
    height: min-content;
    padding:0;
  }

  #cadreStats{
    width: 100%;
    padding: 0 20px;
  }

  button{
    outline: none;
    border: none;
    padding: 10px 20px;
    color: white;
    background-color: rgb(190, 190, 190);
    margin-top: 50px;
    border-radius: 5px;

    &:hover{
      cursor: pointer;
    }
  }
}
