.content{
  width: 30%;
  height: 100%;
  border: 2px solid black;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  h3, h6{
    margin-bottom: 20px;
  }

  input{
    outline: none;
    border: none;
    border-bottom: 2px solid black;
    background-color: transparent;
    color: black;
    width: 150px;
    padding: 10px;
    text-align: center;
    transition: border-color .6s;

    &[disabled]{
      color: white;
      background-color: grey;
    }

    &:focus{
      border-color: blue;
    }
  }

  button{
    margin-top: 50px;
    margin-bottom: 50px;
    outline: none;
    border: none;
    color: white;
    background-color: black;
    padding: 10px 20px;
    border-radius: 5px;
    font-weight: bold;
    font-size: 120%;

    &:hover{
      cursor: pointer;
    }
  }
}