*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Arial, sans-serif;

  #intro{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;

    img{
      transform: translateX(5px);
      margin-top: 50px;
      margin-bottom: 50px;
    }

    .flex{
      display: flex;
      width: 90%;
      justify-content: space-around;

      .choose-j{
        display: flex;
        flex-direction: column;
        height: min-content;
      }
    }

    input{
      outline: none;
      border: none;
      border-bottom: 2px solid black;
      background-color: transparent;
      color: black;
      width: 150px;
      padding: 10px;
      text-align: center;
      transition: border-color .6s;

      &[disabled]{
        color: white;
        background-color: grey;
      }

      &:focus{
        border-color: blue;
      }
    }

    button{
      outline: none;
      border: none;
      padding: 10px 20px;
      color: white;
      background-color: rgb(190, 190, 190);
      border-radius: 5px;
      margin-top: 30px;

      &:hover{
        cursor: pointer;
      }
    }
  }

  #game{
    width: 100vw;
    height: 100vh;
    display: flex;
  }
}